<template>
    <div>
        <div class="per">
            <el-row type="flex" class="row-bg" justify="end">
                <el-col :span="3">
                    <el-form class="order">
                        <el-image :src="USER" style="width:35px;height: 30px"></el-image >
                        {{user}}
                    </el-form>
                </el-col>
                <el-col :span="2">
                    <el-tooltip class="item" effect="dark" content="退出，回到登录界面">
                        <el-button size="" round class="filter-item" style="width: 90px" type="primary" @click="Quit()">注销</el-button>
                    </el-tooltip>
                </el-col>
                <el-col :span="2">
                    <el-button size="" class="filter-item" round style="width: 100px" type="primary" @click="Modify()">修改密码</el-button>
                </el-col>
            </el-row>
        </div>
        <div class="demo-image">
            <el-image :src="lg" style="width:600px;height: 250px"></el-image >
        </div>
        <el-row type="flex">
            <el-col span="24">
                <el-form label-position="lab" :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="400px" class="demo-ruleForm">
                    <el-form-item prop="question" label="题目问题：">
                        <el-input type="textarea" autosize placeholder="请输入题目问题" v-model="ruleForm.question" autocomplete="off" style="width: 100%"></el-input>
                    </el-form-item>
                    <el-form-item prop="standard_answer" label="标准答案：">
                        <el-input type="textarea" autosize placeholder="请输入标准答案" v-model="ruleForm.standard_answer" autocomplete="off" style="width: 100%"></el-input>
                    </el-form-item>
                    <el-form-item prop="keywords" label="关 键 词 ：">
                        <el-input type="textarea" autosize placeholder="请输入标准答案关键词,关键词间用中文逗号隔开 如：关键词1，关键词2" v-model="ruleForm.keywords" autocomplete="off" style="width: 100%"></el-input>
                    </el-form-item>
                    <el-form-item prop="student_answer" label="学生答案：">
                        <el-input type="textarea" autosize placeholder="请输入学生答案" v-model="ruleForm.student_answer" autocomplete="off"  style="width: 100%" ></el-input>
                    </el-form-item>
                        <el-form-item>
                            <el-button style="width: 140px" type="primary" @click="submitForm('ruleForm')">提交</el-button>
                            <el-button style="width: 140px" @click="resetForm('ruleForm')">重置</el-button>
                        </el-form-item>
                </el-form>
            </el-col>
            <el-col span="6" :offset="1">
                <el-form>
                    <el-form-item>
                        <div class="scor">学生得分</div>
                        <div class="grade" v-loading="loading" element-loading-text="拼命评阅中" element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.7)"><span style="color: crimson">{{grade}}</span></div>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <!--<el-header></el-header>-->
        <!--<el-header></el-header>-->
    </div>
</template>

<script>
    import Cookies from 'js-cookie'
    export default {
        methods: {
            submitForm(formName) {
                const _this=this;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        console.log(_this.ruleForm)
                        _this.$confirm('请确认关键词间已用中文逗号隔开！', '提示', {
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            type: 'warning',
                            roundButton:true
                        }).then(() => {
                            _this.loading=true,
                            axios.post('/patriot/judge',_this.ruleForm).then(function (resp) {
                                if(resp){
                                    _this.loading=false
                                }
                                _this.grade=resp.data
                                console.log(_this.grade)
                            })
                            // create:{
                            //     const loading = _this.$loading({
                            //         lock: true,
                            //         text: '拼命评阅中',
                            //         spinner: 'el-icon-loading',
                            //         background: 'rgba(0, 0, 0, 0.7)'
                            //     });
                            //     if(_this.grade!==''){
                            //         loading.close();
                            //     }
                            //     setTimeout(() => {
                            //         loading.close();
                            //     }, 2000);
                            // }
                            _this.$message({
                                type: 'success',
                                message: '提交成功!',
                            });
                        }).catch(() => {
                            _this.$message({
                                type: 'info',
                                message: '已取消提交'
                            });
                        });
                    }else{
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.grade=''
                this.$refs[formName].resetFields();
            },
            Quit(){
                this.$router.push('/login')
                Cookies.set('user','')
            },
            Modify(){
                this.$router.push('/updatepassword')
            }
        },
        data() {
            var checkQuestion = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '请输入题目问题！',
                        type: 'error',
                        position: 'bottom-left',
                        offset:300
                    });
                    return callback(new Error(' '));
                } else {
                    callback();
                }
            };
            var checkStandard_answer = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '请输入标准答案！',
                        type: 'error',
                        position: 'bottom-left',
                        offset:300
                    });
                    return callback(new Error(' '));
                } else {
                    callback();
                }
            };
            var checkKeywords = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '请输入标准答案关键词！',
                        type: 'error',
                        position: 'bottom-left',
                        offset:300
                    });
                    return callback(new Error(' '));
                }else {
                    callback();
                }
            };
            var checkStudent_answer = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '请输入学生答案！',
                        type: 'error',
                        position: 'bottom-left',
                        offset:300
                    });
                    return callback(new Error(' '));
                }else {
                    callback();
                }
            };
            return {
                // tableData:[{
                //     grade:'暂无数据'
                // }],
                loading:false,
                lab:'right',
                grade:'',
                USER: require("@/assets/lg.png"),
                user:Cookies.get('user'),
                lg: require("@/assets/LOGO2.png"),
                ruleForm: {
                    question: '',
                    standard_answer: '',
                    keywords: '',
                    student_answer:'',
                },
                rules: {
                    question: [
                        { validator: checkQuestion, trigger: 'blur' }
                    ],
                    standard_answer: [
                        { validator: checkStandard_answer, trigger: 'blur' }
                    ],
                    keywords: [
                        { validator: checkKeywords, trigger: 'blur' }
                    ]
                    ,
                    student_answer: [
                        { validator: checkStudent_answer, trigger: 'blur' }
                    ]
                }
            };
        }
    }
</script>

<style>
    body{
        width: 100%;
    }
    .grade{
        font-size: 45px;
    }
    .scor{
        font-size: 20px;
        font-weight: 700;
        width: 100%;
    }
    .el-icon-loading{
        font-size: 25px;
    }
</style>