import Vue from 'vue'
import VueRouter from 'vue-router'
import register from '../views/register.vue'
import login from '../views/login'
import judge from '../views/judge'
import authority from '../views/authority'
import LoginOrRegister from "../views/LoginOrRegister"
import findback from '../views/findback'
import personal from '../views/personal'
import updatepassword from '../views/updatepassword'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'LoR',
        component: LoginOrRegister,
        redirect:"/login",   ////决定首页是login界面
        children: [
            {
                path: '/register',
                name: 'register',
                component: register
            },
            {
                path: '/login',
                name: 'login',
                component:login,

            },
            {
                path:'/findback',
                component:findback
            }
        ]
    },
    {
        path: '/authority',
        name: 'authority',
        component: authority
    },
    {
        path:'/',
        component:personal,
        redirect:"/judge",
        children: [
            {
                path: '/judge',
                name: 'judge',
                component:judge,
                meta: {
                    needLogin: true //需要加校检判断的路由
                },
            },
            {
                path: '/updatepassword',
                name: 'updatepassword',
                component:updatepassword,
                meta: {
                    needLogin: true //需要加校检判断的路由
                },
            },
        ],
        meta: {
            needLogin: true //需要加校检判断的路由
        },
    },
]

const router = new VueRouter({
  mode: '',
  base: process.env.BASE_URL,
  routes
})

export default router
