<script>
    import Cookies from 'js-cookie'
    export default {
        data() {
            var checkname = (rule, value, callback) => {
                if (!value) {
                    this.$notify({
                        message: '用户名不能为空！',
                        type: 'error',
                        offset:100
                    });
                    callback(new Error('    '))
                }else{
                    callback();
                }
            };
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '请输入密码！',
                        type: 'error',
                        offset:200
                    });
                    callback(new Error('    '))
                } else {
                    callback();
                }
            };
            return {
                Img: require("@/assets/home.png"),
                ruleForm: {
                    username: '',
                    password: ''
                },
                rules: {
                    username: [
                        { validator: checkname, trigger: 'blur' }
                    ],
                    password: [
                        { validator: validatePass, trigger: 'blur' }
                    ]
                }
            };
        },
        methods: {
            submitForm(formName) {
                const _this=this;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        console.log(_this.ruleForm)
                        //Cookies.set('user',_this.ruleForm.username)
                        //console.log(Cookies.get('user'))
                        axios.post('/patriot/login',_this.ruleForm).then(function (resp) {
                            switch (resp.data) {
                                case 0:
                                    _this.$message({
                                        showClose: true,
                                        message: '用户名或密码错误！',
                                        type: 'error'
                                    });
                                    break;
                                case 1:
                                    _this.$message({
                                        showClose: true,
                                        message: '登录成功！',
                                        type: 'success'
                                    });
                                    Cookies.set('user',_this.ruleForm.username) //存用户名
                                    console.log(Cookies.get('user'))
                                    _this.$router.push('/judge')
                                    break;
                                default:
                                    _this.$message({
                                        showClose: true,
                                        message: '操作失败，请重试！',
                                        type: 'error'
                                    });
                            }
                        })
                            .catch(function (error) {
                                console.log(error);
                            })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            register() {
                this.$router.push('/register')
            },
            forgrtPass(){
                this.$router.push('/findback')
            },
        },
    }
</script>
