<template>
    <div>
        <div class="per">
            <el-row type="flex" class="row-bg" justify="end">
                <el-col :span="3">
                    <el-form class="order">
                        <el-image :src="USER" style="width:35px;height: 30px"></el-image >
                        {{user}}
                    </el-form>
                </el-col>
                <el-col :span="2">
                    <el-tooltip class="item" effect="dark" content="退出，回到登录界面">
                        <el-button size="" round class="filter-item" style="width: 100px" type="primary" @click="Quit()">注销</el-button>
                    </el-tooltip>
                </el-col>
                <el-col :span="2">
                    <el-button size="" round class="filter-item" style="width: 100px" type="primary" @click="Back()">返回</el-button>
                </el-col>
            </el-row>
        </div>
        <div class="demo-image">
            <el-image :src="lg" style="width:600px;height: 250px"></el-image >
        </div>
        <el-row type="flex">
            <el-col span="18">
                <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="500px" class="demo-ruleForm">
                    <el-form-item prop="">
                    </el-form-item>
                    <el-form-item prop="">
                    </el-form-item>
                    <el-form-item prop="password" label="你的旧密码：">
                        <el-input prefix-icon="el-icon-lock" placeholder="请输入旧密码" type="password" v-model="ruleForm.password" autocomplete="off" style="width: 100%" show-password></el-input>
                    </el-form-item>
                    <el-form-item prop="newpassword" label="设置新密码：">
                        <el-input prefix-icon="el-icon-lock" placeholder="请设置密码" type="password" v-model="ruleForm.newpassword" autocomplete="off" style="width: 100%" show-password></el-input>
                    </el-form-item>
                    <el-form-item prop="checkPass" label="确认新密码：">
                        <el-input prefix-icon="el-icon-lock" placeholder="请再次输入新密码确认" type="password" v-model="ruleForm.checkPass" autocomplete="off"  style="width: 100%" show-password></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button style="width: 150px" type="primary"@click="submitForm('ruleForm')">确认修改</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-header></el-header>
        <el-header></el-header>
    </div>
</template>

<script>
    import Cookies from 'js-cookie'
    export default {
        methods: {
            submitForm(formName) {
                const _this=this;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        console.log(_this.ruleForm)
                        axios.post('/patriot/updatepassword',_this.ruleForm).then(function (resp) {
                            if(resp.data=='1'){
                                _this.$message({
                                    showClose: true,
                                    message: '修改成功，请重新登陆！',
                                    type: 'success'
                                });
                                _this.$router.push('/login')
                            }else if(resp.data=='0'){
                                _this.$message({
                                    showClose: true,
                                    message: '原密码错误！',
                                    type: 'error'
                                });
                            }else{
                                _this.$message({
                                    showClose: true,
                                    message: '操作失败，请重试！',
                                    type: 'error'
                                });
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            Back(){
                this.$router.push('/judge')
            },
            Quit(){
                this.$router.push('/login')
                Cookies.set('user','')
            },
            Modify(){
                this.$router.push('/updatepassword')
            }
        },
        data(){
            var checkpass = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '请设置密码！',
                        type: 'error',
                        offset:150
                    });
                    return callback(new Error(' '));
                } else {
                    if (this.ruleForm.checkPass !== '') {
                        this.$refs.ruleForm.validateField('checkPass');
                    }
                    callback();
                }
            };
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '请设置新密码！',
                        type: 'error',
                        offset:250
                    });
                    return callback(new Error(' '));
                } else if(value.length<=5||value.length>=15){
                    this.$notify({
                        message: '新密码必须是6-16位！',
                        type: 'error',
                        offset:250
                    });
                    return callback(new Error(' '));
                }else{
                    if (this.ruleForm.checkPass !== '') {
                        this.$refs.ruleForm.validateField('checkPass');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '请再次输入密码！',
                        type: 'error',
                        offset:350
                    });
                    return callback(new Error(' '));
                } else if (value !== this.ruleForm.newpassword){
                    this.$notify({
                        message: '两次密码不一致！',
                        type: 'error',
                        offset:450
                    });
                    return callback(new Error(' '));
                } else {
                    callback();
                }
            };
            return{
                USER: require("@/assets/lg.png"),
                user:Cookies.get('user'),
                lg: require("@/assets/LOGO2.png"),
                ruleForm:{
                    password:'',
                    newpassword:'',
                    checkPass:''
                },
                rules: {
                    password: [
                        { validator: checkpass, trigger: 'blur' }
                    ],
                    newpassword: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    checkPass: [
                        { validator: validatePass2, trigger: 'blur' }
                    ],
                }
            }
        }
    }
</script>

<style scoped>

</style>