<template>
    <div>
        <div>
            <el-row type="flex" class="row-bg" justify="end">
                <el-col :span="6">
                    <div class="right-items" style="float: right;">
                        <el-header></el-header>
                        <!--<el-button round class="filter-item" style="width: 70px" type="primary" @click="login()" placeplacement="left-end">登录</el-button>-->
                        <!--<el-button class="filter-item" round style="width: 70px" type="primary" @click="register()" placeplacement="right-end">注册</el-button>-->
                    </div>
                </el-col>
            </el-row>
            <div class="demo-image">
                <el-image :src="Img" style="width:400px;height: 150px"></el-image >
            </div>
            <h4> 这次修改的密码要好好记住哦^-^</h4>
        </div>
        <el-row type="flex">
            <el-col span="18">
                <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="500px" class="demo-ruleForm">
                    <el-form-item prop="username" label="你的昵称：">
                        <el-input prefix-icon="el-icon-user" placeholder="请输入你的昵称" v-model.number="ruleForm.username" style="width: 100%"></el-input>
                    </el-form-item>
                    <el-form-item prop="realname" label="你的姓名：">
                        <el-input prefix-icon="el-icon-user" placeholder="请输入你的真实姓名" v-model="ruleForm.realname" autocomplete="off" style="width: 100%"></el-input>
                    </el-form-item>
                    <el-form-item prop="email" label="你的邮箱：">
                        <el-input prefix-icon="el-icon-message" placeholder="请填写邮箱地址" v-model="ruleForm.email" style="width: 100%"></el-input>
                    </el-form-item>
                    <el-form-item  prop="worknumber" label="你的工号：">
                        <el-input prefix-icon="el-icon-collection-tag" placeholder="请填写你的工号" v-model="ruleForm.worknumber" style="width: 100%"></el-input>
                    </el-form-item>
                    <el-form-item prop="newpassword" label="设置新密码：">
                        <el-input prefix-icon="el-icon-lock" placeholder="请设置密码" type="password" v-model="ruleForm.newpassword" autocomplete="off" style="width: 100%" show-password></el-input>
                    </el-form-item>
                    <el-form-item prop="checkPass" label="确认新密码：">
                        <el-input prefix-icon="el-icon-lock" placeholder="请再次输入密码" type="password" v-model="ruleForm.checkPass" autocomplete="off"  style="width: 100%" show-password></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button class="fb" style="width: 150px" type="primary" @click="submitForm('ruleForm')">确认修改</el-button>
                        <el-button class="filter" style="width: 150px" @click="login()">返回首页</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <!--<el-header></el-header>-->
        <!--<el-header></el-header>-->
        <!--<el-header></el-header>-->
        <!--<el-header></el-header>-->
    </div>
</template>

<script>
    export default {
        data() {
            var checkname = (rule, value, callback) => {
                if (!value) {
                    this.$notify({
                        message: '昵称不能为空！',
                        type: 'error',
                        offset: 50
                    });
                    callback(new Error('    '))
                } else {
                    callback();
                }
            };
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '请设置新密码！',
                        type: 'error',
                        offset:450
                    });
                    return callback(new Error(' '));
                } else {
                    if (this.ruleForm.checkPass !== '') {
                        this.$refs.ruleForm.validateField('checkPass');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '请再次输入密码！',
                        type: 'error',
                        offset:550
                    });
                    return callback(new Error(' '));
                } else if (value !== this.ruleForm.newpassword) {
                    this.$notify({
                        message: '两次密码不一致！',
                        type: 'error',
                        offset:350
                    });
                    return callback(new Error(' '));
                } else {
                    callback();
                }
            };
            var checkMail = (rule, value, callback) => {
                const regEmail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
                if (value !=='' && !regEmail.test(value)) {
                    this.$notify({
                        message: '邮箱格式不正确!',
                        type: 'error',
                        offset: 250
                    });
                    this.ruleForm.email = null
                    return callback(new Error('  '));
                }else {
                    callback();
                }
            };
            var checkNumber = (rule, value, callback) => {
                if (!value) {
                    this.$notify({
                        message: '工号不能为空！',
                        type: 'error',
                        offset: 350
                    });
                    return callback(new Error(' '));
                }else{
                    callback()
                }
            };
            var checkrealname = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '真实姓名不能为空！',
                        type: 'error',
                        offset: 150
                    });
                    callback(new Error('    '))
                } else {
                    callback();
                }
            };
            return {
                Img: require("@/assets/LOGO2.png"),
                ruleForm: {
                    username: '',
                    realname: '',
                    email:null,
                    worknumber:'',
                    newpassword:'',
                    checkPass:''
                },
                rules: {
                    realname: [
                        { validator: checkrealname, trigger: 'blur' }
                    ],
                    username: [
                        { validator: checkname, trigger: 'blur' }
                    ],
                    email:[
                        {validator: checkMail, trigger: 'blur'}
                    ],
                    worknumber: [
                        { validator: checkNumber, trigger: 'blur' }
                    ],
                    newpassword: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    checkPass: [
                        { validator: validatePass2, trigger: 'blur' }
                    ]
                }
            };
        },
        methods: {
            submitForm(formName) {
                const _this=this;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        console.log(_this.ruleForm)
                        axios.post('/patriot/findback',_this.ruleForm).then(function (resp) {
                            if(resp.data=='1'){
                                _this.$notify({
                                    showClose: true,
                                    message: '修改成功，请重新登陆！',
                                    type: 'success'
                                });
                                _this.$router.push('/login')
                            }else if(resp.data=='0'){
                                _this.$notify({
                                    showClose: true,
                                    message: '输入信息不正确，请重试！',
                                    type: 'error'
                                });
                            }else{
                                _this.$message({
                                    showClose: true,
                                    message: '操作失败，请重试！',
                                    type: 'error'
                                });
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            register() {
                this.$router.push('/register')
            },
            login() {
                this.$router.push('/login')
            },
        }
    }
</script>

<style scoped>
    .fb{
       background: cornflowerblue;
    }
</style>