<template>
  <div>
      <div>
          <el-row type="flex" justify="end">
              <el-col :span="6">
                  <div class="right-items" style="float: right;">
                      <el-header></el-header>
                  </div>
              </el-col>
          </el-row>
          <div class="demo-image">
              <el-image :src="Img" style="width:400px;height: 150px"></el-image >
          </div>
          <h3>欢迎使用pAtrIOT智能评阅系统</h3>
      </div>
      <el-row type="flex">
          <el-col span="18">
              <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="500px" class="demo-ruleForm">
                  <el-form-item prop="username" label="新用户名：">
                      <el-input prefix-icon="el-icon-user" placeholder="请输入新用户名" v-model.number="ruleForm.username" style="width: 100%"></el-input>
                  </el-form-item>
                  <el-form-item prop="password" label="设置密码：">
                      <el-input prefix-icon="el-icon-lock" placeholder="请设置6-16位密码" type="password" v-model="ruleForm.password" autocomplete="off" style="width: 100%" show-password></el-input>
                  </el-form-item>
                  <el-form-item prop="checkPass" label="确认密码：">
                      <el-input prefix-icon="el-icon-lock" placeholder="请再次输入密码确认" type="password" v-model="ruleForm.checkPass" autocomplete="off"  style="width: 100%" show-password></el-input>
                  </el-form-item>
                  <el-form-item  prop="email" label="输入邮箱：">
                      <el-input prefix-icon="el-icon-message" placeholder="请填写邮箱地址" v-model="ruleForm.email" style="width: 100%"></el-input>
                  </el-form-item>
                  <el-form-item prop="realname" label="真实姓名：">
                      <el-input prefix-icon="el-icon-user" placeholder="请输入新用户名" v-model.number="ruleForm.realname" style="width: 100%"></el-input>
                  </el-form-item>
                  <el-form-item  prop="worknumber" label="输入工号：">
                      <el-input prefix-icon="el-icon-collection-tag" placeholder="请填写你的工号" v-model="ruleForm.worknumber" style="width: 100%"></el-input>
                  </el-form-item>
                  <el-form-item class="hhh">
                      <el-button class="reg" style="width: 140px" type="primary" @click="submitForm('ruleForm')">注册</el-button>
                      <el-button class="filter" style="width: 140px" @click="login()" placeplacement="left-end">返回首页</el-button>
                  </el-form-item>
              </el-form>
          </el-col>
      </el-row>
      <!---->
  </div>
</template>

<script>
    export default {
        data() {
            var checkName = (rule, value, callback) => {
                if (!value) {
                    this.$notify({
                        message: '新用户名不能为空！',
                        type: 'error',
                        offset:50
                    });
                    return callback(new Error(' '));
                }
                else {
                    callback()
                };
            };
            var checkreal = (rule, value, callback) => {
                if (value === '') {
                        this.$notify({
                            message: '请输入真实姓名！',
                            type: 'error',
                            offset: 500
                        });
                    return callback(new Error(' '));
                }else{
                    callback()
                }
            }
            var checkNumber = (rule, value, callback) => {
                if (!value) {
                    this.$notify({
                        message: '工号不能为空！',
                        type: 'error',
                        offset: 550
                    });
                    return callback(new Error(' '));
                }else {
                    callback()
                }
            };
            var checkMail = (rule, value, callback) => {
                const regEmail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
                    if (value !=='' && !regEmail.test(value)) {
                        this.$notify({
                            message: '邮箱格式不正确!',
                            type: 'error',
                            offset: 150
                        });
                        this.ruleForm.email = null
                        return callback(new Error('  '));
                    }else {
                        callback();
                    }
            };
            var validatePass = (rule, value, callback) => {
                if (value === ''){
                    this.$notify({
                        message: '请设置密码！',
                        type: 'error',
                        offset:250
                    });
                    return callback(new Error(' '));
                } else if(value.length<=5||value.length>=15){
                    this.$notify({
                        message: '密码必须在6-16位！',
                        type: 'error',
                        offset:250
                    });
                    return callback(new Error(' '));
                }else{
                    if (this.ruleForm.checkPass !== '') {
                        this.$refs.ruleForm.validateField('checkPass');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    this.$notify({
                        message: '请再次输入密码！',
                        type: 'error',
                        offset:350
                    });
                    return callback(new Error(' '));
                } else if (value !== this.ruleForm.password) {
                    this.$notify({
                        message: '两次密码不一致！',
                        type: 'error',
                        offset:450
                    });
                    return callback(new Error(' '));
                } else {
                    callback();
                }
            };
            return {
                Img: require("@/assets/LOGO2.png"),
                ruleForm: {
                    password: '',
                    checkPass: '',
                    username: '',
                    email: null,
                    realname:'',
                    worknumber:''
                },
                rules: {
                    username: [
                        { validator: checkName, trigger: 'blur' }
                    ],
                    password: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    checkPass: [
                        { validator: validatePass2, trigger: 'blur' }
                    ],
                    email: [
                        { validator:checkMail, trigger: 'blur' }
                    ],
                    realname:[
                        {validator: checkreal,trigger:'blur'}
                    ],
                    worknumber:[
                        {validator: checkNumber, trigger: 'blur'}
                    ]
                }
            };
        },
        methods: {
            submitForm(formName) {
                const _this=this;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        axios.post('/patriot/register',_this.ruleForm).then(function (resp) {
                            if(resp.data=='1'){
                                _this.$message({
                                    showClose: true,
                                    message: '注册成功！',
                                    type: 'success'
                                });
                                _this.$router.push('/login')
                            }else if(resp.data=='0'){
                                _this.$message({
                                    showClose: true,
                                    message: '该用户名已被注册！',
                                    type: 'error'
                                });
                                callback(new Error('  '))
                            }else{
                                _this.$message({
                                    showClose: true,
                                    message: '操作失败，请重试！',
                                    type: 'error'
                                });
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            login() {
                this.$router.push('/login')
            },
            forgrtPass(){
                this.$router.push('/findback')
            },
        }
    }
</script>

<style scoped>
    .reg{
        background: cornflowerblue;
    }
    .hhh{
        position: -ms-device-fixed;
    }
</style>